.players-container .players-detials {
	display: -webkit-flex;
	display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 20px 0px;
}

.players-container .players-detials p {
	margin: 0 37px;
}

table td {
	border: 2px solid #545151;
    width: 100px;
    height: 100px;
    border-radius: 10px;
	transition: all 0.3s ease-in-out;
	cursor: pointer;
	text-align: center;
}

table td:hover {
	background-color: #7effd4;
}
.container button {
	width: 186px;
    padding: 8px;
    margin-top: 18px;
    background-color: #880f3c;
    color: #ffffff;
    border: none;
    cursor: pointer;
    border-radius: 10px;
	transition: all 0.3s ease-in-out;
}

.container button:hover {
	background-color: #e525d6;
}
/* Start Global Style*/

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Cairo";
}
/* End Global Style*/

.container {
    display: -webkit-flex;
    display: flex;
    position: relative;
    top: 100px;
    color: #fff;
    margin: auto 10px;
}

.container .form {
    background-color: rgb(255 108 243 / 8%);
    padding: 20px;
    border-radius: 10px;
}

.container h2 {
    background-color: #880f3c;
    padding: 5px;
    margin: 10px 0;
    border-radius: 7px;
}

.player-name {
    width: 30%;
}

.inputs {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.inputs label {
    display: block;
    margin: 10px 0;
}
.container .form input[type="text"] {
    padding: 12px;
    border: none;
    border-radius: 3px;
    outline: none;
    color: #e525d6;
    border-bottom: 2px solid #e525d6;

}

.container .form .field:first-child {
    margin-right: 12px;
}

.container .form button {
    display: block;
    margin: 15px auto;
    width: 100%;
    background: #880f3c;
    border: none;
    padding: 12px;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    outline: none;
    transition: all 0.3s ease-in-out;
}

.container .form button:hover {
    background: #f12a73;
}

.container .form button .link {
    text-decoration: none;
    color: #fff;
}

